/* MEDIA QUERY BREAK POINTS */
@xSmallBreakpoint: 480px;
@smallBreakpoint: 768px;
@mediumBreakpoint: 992px;
@largeBreakpoint: 1200px;

/* ==================================== >>
THEMING
 << =================================== */
:root {
    .mx-theme-light;
}

// ****************************************** //
// **** EN-DISABLE COLOR MODE PROPERTIES **** //
@colorModes: true;
// ****************************************** //
// ****************************************** //

// OS OR BROWSER WANTS DARK THEME
@media (prefers-color-scheme: dark) {
    :root {
        & when (@colorModes = true) {
            .mx-theme-dark;
        }
    }
}

// DARK THEME
@color-theme-dark: color-theme-dark;

.@{color-theme-dark} {
    & when (@colorModes = true) {
        .mx-theme-dark;
    }
}

.mx-dark-mode-rules(@darkmodeProps) {
    @media (prefers-color-scheme: dark) {
        @darkmodeProps();
    }
    .@{color-theme-dark} & {
        @darkmodeProps();
    }
}

// LIGHT THEME
@color-theme-light: color-theme-light;

.@{color-theme-light} {
    & when (@colorModes = true) {
        .mx-theme-light;
    }
}

.mx-light-mode-rules(@lightmodeProps) {
    @media (prefers-color-scheme: light) {
        @lightmodeProps();
    }
    .@{color-theme-light} & {
        @lightmodeProps();
    }
}

// Assign custom properties to allow switching themes on runtime
@ci-color1: var(--ci-color1);
@ci-hovercolor1: var(--ci-hovercolor1);
@bg-hover: var(--bg-hover);
@bg-active: var(--bg-active);
@fg-hover: var(--fg-hover);

@fg-controls: var(--fg-controls);
@border: var(--border);

@bg-general: var(--bg-general);
@fg-general: var(--fg-general);
// Link colors outside of content boxes
@lk-general: var(--lk-general);
@lh-general: var(--lh-general); // hover
// Link colors in content boxes
@lk-content-box: var(--lk-content-box);
@lh-content-box: var(--lh-content-box); // hover
// Header text color
@fg-header: var(--fg-header);
@bg-content-box: var(--bg-content-box);
@fg-content-box: var(--fg-content-box);
