/* ==============================================================
   LESS Mixins
   ==============================================================*/
/* CLEARFLOATS */
.mx-clearfloats {
    &:before,
    &:after {
        content: ' ';
        display: table;
    }
    &:after {
        clear: both;
    }
}
/* BACKGROUND IMAGE */
.mx-bgi(@image; @pX: center; @pY: center; @size: auto) {
    background-image: url(@image);
    background-repeat: no-repeat;
    background-position: @pX @pY;
    -webkit-background-size: @size;
    background-size: @size;
}
/* CSS TRIANGLE */
.mx-triangle(@top: 0; @left: 0; @bWidth: 10px; @bcTop: transparent; @bcRight: transparent; @bcBottom: #000; @bcLeft: transparent) {
    position: relative;
    &:after {
        content: '';
        display: block;
        position: absolute;
        top: @top;
        left: @left;
        width: 0;
        height: 0;
        border-width: @bWidth;
        border-style: solid;
        border-color: @bcTop @bcRight @bcBottom @bcLeft;
    }
}
/* HIDDEN */
.mx-visible {
    display: block !important;
    visibility: visible !important;
}
/* HIDDEN */
.mx-hidden {
    display: none !important;
    visibility: hidden !important;
}
/* VISUALLY HIDDEN */
.mx-v-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    clip: rect(1px, 1px, 1px, 1px);
    white-space: nowrap;
    overflow: hidden;
}
/* GRID */
.mx-grid(@width: 145px; @gap: 15px) {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(@width, 1fr));
    grid-gap: @gap;
}
/* LIST RESET */
.mx-list-reset {
    margin: 0;
    padding: 0;
    list-style: none;
}

/* SVG ICON MASK */
.mx-icon-mask(@iconName; @iconGroup: outline; @library: resources; @basePath: '../../../../icons') {
    @iconPath: ~'@{basePath}/@{iconGroup}/@{iconName}.svg';
    @iconResource: ~'@{iconPath}?ln=@{library}';
    mask: url('@{iconResource}') center/100% 100% no-repeat;
    -webkit-mask: url('@{iconResource}') center/100% 100% no-repeat;
}

// FOCUSRING
.mx-focus-visible {
    outline: 2px solid @colorOutline;
    outline-offset: -2px;
    z-index: 9999;
}

// COLOR SCHEMES

.mx-theme-light {
    color-scheme: light;

    --ci-color1: #015999;
    --ci-hovercolor1: #1b70b1;
    --bg-hover: #eee;
    --bg-active: #3b4557;
    --fg-hover: #212529;
    --fg-controls: #6f6f6f;
    --fg-popover: #000;
    --bg-popover: #fff;
    --border: 1px solid #ccc;
    --bg-general: #f2f3f4;
    --fg-general: #212529;
    --lk-general: #3365a9;
    --lh-general: #444;
    --fg-header: #444;
    --bg-content-box: #fff;
    --fg-content-box: #212529;
    --lk-content-box: #3365a9;
    --lh-content-box: #444;

    /* WIDGET ICON COLORS */
    // --widget-icon-color-topbar: red;
    // --widget-icon-color-body: green;
    // --widget-icon-color-expand: yellow;

    // /* LOGIN NAVIGATION */
    --login-nav-background: #444;
    --login-nav-background-hover: #5e5e5e;
    --login-nav-text-color: #fff;
    --login-nav-text-color-hover: #fff;
    --login-nav-icon-color: #fff;
    --login-nav-icon-color-hover: #fff;
    --login-nav-bookmarks-background: #3a3a3a;
    --login-nav-bookmarks-text-color: #fff;
    --login-nav-bookmarks-text-color-hover: #ffd65b;
    --login-nav-bookmarks-counter-background: #015999;
    --login-nav-dropdown-border-color: rgba(255, 255, 255, 0.1);

    /* SEARCH TOOLBAR */
    --search-toolbar-background-active: var(--ci-color1);
    --search-toolbar-text-color-active: @white;
    --search-toolbar-divider-color: rgba(31, 41, 55, 0.28);
    --search-toolbar-icon-color: var(--fg-general);
    --search-toolbar-icon-color-active: @white;
    --search-toolbar-icon-color-hover: var(--fg-general);

    /* IMAGE CONTROLS */
    --image-controls-background: var(--bg-content-box);
    --image-controls-border-color: #ccc;
    --image-controls-container-border-color: #ccc;
    --image-controls-icon-color: #6f6f6f;
    --image-controls-icon-color-hover: #5a5a5a;
    --image-controls-icon-color-active: #4a4a4a;
    --image-controls-action-background: transparent;
    --image-controls-action-background-hover: var(--bg-hover);
    --image-controls-slider-thumb-background: #6f6f6f;
    --image-controls-slider-border-color: #6f6f6f;

    /* ARCHIVES LIST */
    --archives-card-bg: var(--bg-content-box);
    --archives-card-border: var(--border);
    --archives-card-shadow-hover: rgba(0, 0, 0, 0.16);
    --archives-title-color: var(--fg-content-box);
    --archives-description-color: var(--fg-controls);
    --archives-overlay-bg: rgba(0, 0, 0, 0.65);
    --archives-overlay-text: #fff;
    --archives-placeholder-bg: var(--bg-general);
    --archives-placeholder-icon-color: var(--fg-controls);
}

.mx-theme-dark {
    color-scheme: dark;

    --ci-color1: #015999;
    --bg-general: #000e29;
    --bg-content-box: #253044;
    --fg-content-box: #e3e4e7;
    --lk-content-box: #e3e4e7;
    --bg-hover: #5b6473;
    --bg-active: #3b4557;
    --ci-hovercolor1: #5b6473;
    --fg-hover: #d8d9dd;
    --fg-controls: #d8d9dd;
    --fg-popover: #fff;
    --bg-popover: #253044;
    --border: 0;
    --fg-general: rgba(255, 255, 255, 0.8);
    --lk-general: rgba(255, 255, 255, 0.8);
    --lh-general: rgba(255, 255, 255, 0.8);
    --fg-header: rgba(255, 255, 255, 0.8);
    --lh-content-box: rgba(255, 255, 255, 0.8);

    /* WIDGET ICON COLORS */
    // --widget-icon-color-topbar: red;
    // --widget-icon-color-body: green;
    // --widget-icon-color-expand: yellow;

    // /* LOGIN NAVIGATION */
    --login-nav-background: #253044;
    --login-nav-background-hover: #3b4557;
    --login-nav-text-color: rgba(255, 255, 255, 0.9);
    --login-nav-text-color-hover: #fff;
    --login-nav-icon-color: rgba(255, 255, 255, 0.9);
    --login-nav-icon-color-hover: #fff;
    --login-nav-bookmarks-background: #1a2535;
    --login-nav-bookmarks-text-color: rgba(255, 255, 255, 0.9);
    --login-nav-bookmarks-text-color-hover: #ffd65b;
    --login-nav-bookmarks-counter-background: #015999;
    --login-nav-dropdown-border-color: rgba(255, 255, 255, 0.05);

    /* SEARCH TOOLBAR */
    --search-toolbar-background-active: var(--ci-color1);
    --search-toolbar-text-color-active: @white;
    --search-toolbar-divider-color: rgba(255, 255, 255, 0.2);
    --search-toolbar-icon-color: rgba(243, 244, 246, 0.92);
    --search-toolbar-icon-color-active: @white;
    --search-toolbar-icon-color-hover: #49a5ff;

    /* IMAGE CONTROLS */
    --image-controls-background: var(--bg-content-box);
    --image-controls-border-color: transparent;
    --image-controls-container-border-color: transparent;
    --image-controls-icon-color: rgba(255, 255, 255, 0.8);
    --image-controls-icon-color-hover: rgba(255, 255, 255, 0.8);
    --image-controls-icon-color-active: rgba(255, 255, 255, 0.8);
    --image-controls-action-background: var(--bg-active);
    --image-controls-action-background-hover: var(--bg-hover);
    --image-controls-slider-thumb-background: #6f6f6f;
    --image-controls-slider-border-color: #6f6f6f;

    /* ARCHIVES LIST */
    --archives-card-bg: var(--bg-content-box);
    --archives-card-border: 1px solid transparent;
    --archives-card-shadow-hover: rgba(0, 0, 0, 0.4);
    --archives-title-color: var(--fg-content-box);
    --archives-description-color: var(--fg-controls);
    --archives-overlay-bg: rgba(0, 0, 0, 0.75);
    --archives-overlay-text: #e3e4e7;
    --archives-placeholder-bg: var(--bg-content-box);
    --archives-placeholder-icon-color: var(--fg-controls);
}

// DETACHED RULESET FOR OVERRIDING STYLING
.detachedRuleSetTopLevel(@generalprops) {
    & {
        @generalprops();
    }
}
