/* ==============================================================
   Main Navigation
   style declarations for crowdsourcingNavigation.xhtml
   ==============================================================*/
#mainnav {
    background-color: @ci-color1;
    border-color: @ci-color1;
    ul {
        li {
            a {
                color: @white;
                &:hover,
                &:focus,
                &:active {
                    color: white;
                }
            }
        }
    }
    .dropdown-menu > li {
        width: 100%;
        &.divider {
            min-height: inherit;
        }
    }
    .dropdown-menu > li > a {
        color: @middleblack;
        &:hover,
        &:focus,
        &:active {
            color: @middleblack;
            background-color: @lightgray;
        }
    }
    .navbar-header {
        .navbar-brand {
            img {
            }
        }
    }
    .navbar-collapse {
        .navbar-right {
            li {
                a {
                    .glyphicon {
                    }
                }
            }
            .dropdown {
                .dropdown-toggle {
                }
                .dropdown-menu {
                    h3 {
                    }
                    li {
                        a {
                            .glyphicon {
                            }
                        }
                    }
                    .divider {
                    }
                }
            }
        }
    }
}

